import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import Skeleton from "react-loading-skeleton";
import notrespond from "../../Images/500.svg";
import Axios from "axios";

const { SearchBar } = Search;
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const server = process.env.REACT_APP_BACKEND_URL;
class EditComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listPindahBarang: [],
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "noBon",
          text: "Nomor Bon",
        },
        {
          dataField: "dari",
          text: "Dari",
        },
        {
          dataField: "ke",
          text: "Ke",
        },
        {
          dataField: "bruto",
          text: "Bruto",
        },
        {
          dataField: "netto",
          text: "Netto",
        },
      ],
    };
  }

  componentDidMount() {
    Axios.get(server + "")
      .then((res) =>
        this.setState({
          listPindahBarang: res.data,
        })
      )
      .catch((res) => console.log(res));
  }
  render() {
    return (
      <div className="row">
        <div className="col-lg-3">
          <Field
            name="tanggal"
            label="Tanggal"
            type="date"
            component={renderField}
          />
        </div>
        <div className="col-lg-3">
          <Field name="dari" label="Dari" type="text" component={renderField} />
        </div>
        <div className="col-lg-3">
          <Field name="ke" label="Ke" type="text" component={renderField} />
        </div>
        <div className="col-lg-3">
          <Field
            name="noBon"
            label="Nomor Bon"
            type="text"
            component={renderField}
          />
        </div>
        <div className="col-lg-12">
          {this.state.listPindahBarang ? (
            <ToolkitProvider
              keyField="id"
              data={this.state.listPindahBarang}
              columns={this.state.columns}
              search
            >
              {(props) => (
                <div className="text-left mb-3">
                  <div className="text-right">
                    <SearchBar
                      {...props.searchProps}
                      className="form-control"
                      placeholder="Pencarian"
                      style={{ margin: 0, top: "50%" }}
                    />
                  </div>
                  <hr />
                  <BootstrapTable
                    {...props.baseProps}
                    striped
                    hover
                    condensed
                    bordered={true}
                    headerClasses="header-table"
                    pagination={paginationFactory()}
                  />
                </div>
              )}
            </ToolkitProvider>
          ) : (
            <div>
              {this.props.error ? (
                <div className="text-center">
                  <img
                    src={notrespond}
                    style={{ width: "30%" }}
                    alt="Not Respond"
                  />
                  <br />
                  <h4>Request API Gagal, Silahkan Refresh</h4>
                </div>
              ) : (
                <Skeleton width={"100%"} height={250} />
              )}
            </div>
          )}
        </div>
      </div>
    );
  }
}

EditComponent = reduxForm({
  form: "EditComponent",
  enableReinitialize: true,
})(EditComponent);
export default connect()(EditComponent);
